.App{
  height:600px;
  width:400px;
  margin:100px auto;
  background-color:darkslateblue;
}
#name{
  text-align:center;
  padding:20px;
}
form{
  background-color: tomato;
  text-align: center;
}
header input{
  outline:none;
  border: none;
  width:200px;
  height:30px;
  padding:5px;
  background-color: transparent;
  color:#fff;
  font-size:18px;
}
button:hover{
  background-color: teal;
}
button:active{
  transform:translateY(1px);
}
header button{
  border: none;
  padding:7px;
  width:80px;
  margin:10px;
}